
.journal__main-content {
    display: flex;
}

.journal__sidebar {
    background-color: $dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding: 0px 10px;
    width: 450px;
}

.journal__sidebar-navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.journal__sidebar-navbar h3 {
    font-weight: lighter;

    i {
        margin-right: 10px;
    }
}

.journal__new-entry {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    cursor: pointer;
    margin-top: 30px;
    width: 100%;

    transition: color .3s ease;

    &:hover {
        color: darken($color: white, $amount: 20);
    }
}

.journal__entries {
    flex: 1 1 auto;
    margin-top: 30px;
    overflow: scroll;
}

.journal__entry {
     background-color: white;
     border-radius: 4px;
     color: $dark-grey;
     display: flex;
     justify-content: space-between;
     margin-bottom: 10px;
     overflow: hidden;
     cursor: pointer;
     padding: 0px 3px;
}

.journal__entry-picture {
    height: 75px;
    width: 75px;
}

.journal__entry-body {
    padding: 10px;
}

.journal__entry-title {
    font-size: 14px;
    font-weight: bold;
}

.journal_entry-content {
    font-size: 10px;
}

.journal__entry-date-box {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 5px;
}

.journal__entry-date-box span {
    font-size: 12px;
}